/* common style */
::selection {
  background: darken($color-primary, 10);
  color: $white;
}

ul {
  list-style-type: none;
  margin: 0px;
}

img {
  vertical-align: middle;
  border: 0;
}

a,
a:hover,
a:focus {
  text-decoration: none;
}

a {
  transition: 0.3s ease;
  color: $color-primary;
  &:hover {
    color: lighten($color-primary, 10);
  }
}

a.text-dark,
a.text-light,
a.text-white {
  &:hover {
    color: $color-primary !important;
  }
}

a,
button {
  cursor: pointer;
}

.outline-0,
.outline-0:focus {
  outline: 0;
}

.d-unset {
  display: unset !important;
}
@media (min-width: 1400px) {
  .container {
    max-width: 1300px;
  }
}
